body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

a {
  color: currentColor;
  text-decoration: none;
}

.search-container {
  width: 100vw;
  z-index: 10;
  background-color: #5454eb;
  position: fixed;
  top: -1px;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, .2), 0 4px 5px rgba(0, 0, 0, .14), 0 1px 10px rgba(0, 0, 0, .12);
}

.search-form {
  min-width: 300px;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding: 8px 0;
  display: flex;
}

.search-input {
  width: 200px;
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
  margin-right: 8px;
  padding: 8px 5px;
  font-size: 16px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.search-input:focus {
  border-color: #fff;
}

.search-input::-ms-input-placeholder {
  font-size: 16px;
}

.search-input::placeholder {
  font-size: 16px;
}

.search-button {
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .15));
  cursor: pointer;
  text-transform: uppercase;
  background-color: rgba(255, 255, 255, .9);
  border: 1px solid rgba(255, 255, 255, .3);
  border-radius: 4px;
  padding: 8px 5px;
  font-size: 16px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.search-button:hover, .search-button:focus {
  background-color: #fff;
}

.gallery {
  grid-gap: 20px;
  max-width: calc(100vw-80px);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 70px auto 30px;
  padding-left: 20px;
  padding-right: 20px;
  display: grid;
}

.photo-card {
  height: 287px;
  background-color: #fff;
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.photo-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

.info {
  justify-content: space-between;
  padding: 20px;
  display: flex;
}

.info-item {
  color: #757575;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.info-item b {
  color: #000;
  margin-bottom: 10px;
}

.load-more.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.load-more {
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .15));
  text-transform: uppercase;
  cursor: pointer;
  color: #fff;
  background-color: #5454eb;
  border: 1px solid rgba(255, 255, 255, .3);
  border-radius: 4px;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
  padding: 8px 12px;
  font-size: 16px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.load-more:hover, .load-more:focus {
  background-color: #9797c9;
}

