body {
  margin: 0;

  font-family: 'Roboto', sans-serif;
  font-size: 16px;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

a {
  text-decoration: none;
  color: currentColor;
}

.search-container {
  width: 100vw;
  position: fixed;
  top: -1px;
  z-index: 10;

  background-color: rgb(84, 84, 235);
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.search-form {
  display: flex;
  align-items: center;
  justify-content: center;

  margin-left: auto;
  margin-right: auto;

  padding: 8px 0;
  min-width: 300px;
}

.search-input {
  width: 200px;
  padding: 8px 5px;
  margin-right: 8px;
  border-radius: 4px;
  outline: transparent;

  font-size: 16px;

  transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.search-input:focus {
  border-color: #ffffff;
}

.search-input::placeholder {
  font-size: 16px;
}

.search-button {
  padding: 8px 5px;
  border-radius: 4px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.15));

  font-size: 16px;
  cursor: pointer;
  text-transform: uppercase;

  background-color: rgba(255, 255, 255, 0.9);

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.search-button:hover,
.search-button:focus {
  background-color: #fff;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 20px;
  max-width: calc(100vw-80px);

  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
  margin-top: 70px;
  padding-left: 20px;
  padding-right: 20px;
}

.photo-card {
  height: 287px;
  overflow: hidden;

  background-color: #ffffff;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 4px 4px 4px 4px;
}

.photo-card img {
  display: block;
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.info {
  padding: 20px;

  display: flex;
  justify-content: space-between;
}

.info-item {
  display: flex;
  flex-direction: column;
  align-items: center;

  color: #757575;
}

.info-item b {
  margin-bottom: 10px;

  color: black;
}

.load-more.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.load-more {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
  padding: 8px 12px;
  border-radius: 4px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.15));

  font-size: 16px;
  text-transform: uppercase;
  cursor: pointer;

  background-color: rgb(84, 84, 235);
  color: #fff;

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.load-more:hover,
.load-more:focus {
  background-color: rgb(151, 151, 201);
}
